

.hero {
    background-color: var(--overlay);
    color: #fff;
    height: 100vh;
    width: 100%;
    position: relative;
}

.hero:before {
    content: '';
    background: url('../images/close_lid.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

.hero .content p:first-child {
    font-style: italic;
    font-size: .9rem;
}

.hero .content p:nth-child(2) {
    font-size: 1.9rem;
}

.hero .content p:nth-child(3) {
    font-size: 5rem;
    font-weight: 700;
}


.hero .content p:nth-child(4) {
    font-size: 2.2rem;
    font-style: italic;
}

.hero .button {
    margin: 1rem 0;
    width: 300px;
    background-color: var(--secondary-color);
    border: 1px solid var(--secondary-color);
    color: #333;
}

.hero .button:hover {
    background-color: var(--secondary-dark);
    border: 1px solid var(--secondary-dark);
}

@media screen and (max-width:940px) {
    .hero .content p:nth-child(3) {
        font-size: 2.6rem;
    }

    .hero .content p:nth-child(4) {
        font-size: 1.6rem;
    }
}
/* =====-------------------------------------------------------------- */
.about {
    background-color: #ffffff;;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    width: 100%;
}

.about .container {
    width: 1050px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.about h2 {
    font-size: 3rem;
}

.about .col-2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: 2.5rem;
}

.about .col-2 p:first-of-type {
    margin: 2rem 0;
}
/* 
.about .col-2 p:nth-child(4) {
    font-size: 1.5rem;
} */

@media screen and (max-width:940px) {
    .about .container {
        grid-template-columns: 1fr;
    }

    .about .container {
        width: 100%;
    }

    .about .container img {
        padding: 0rem;
        width: 90vw;
        z-index: 1;
    }

    .about .container .col-2 {
        padding: 0;
    }

    .about button {
        margin-bottom: 1rem;
    }
}

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

.home_Container {
  padding: 50px 0px 0px 50px;
}

.home_Container_box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 130px 0 200px 0;
}

.home_box_img {
  height: 330px;
  width: 300px;

  cursor: pointer;
}
.home_box_img img {
  height: 90%;
  width: 100%;
  border-radius: 50%;
}

/* ------------------------ Home text--------------------------- */

.home_box_content h5 {
  font-size: 40px;
  font-weight: 650;
  color:rgb(184, 208, 212)
}

.home_box_content h2 {
  font-size: 80px;
  font-weight: 700;
  color: white;
}

.full {
  display: flex;
  align-items: center;
  font-size: 34px;
}

.home_box_content p {
  margin-top: -20px;
  font-weight: 700;
}
.home_box_content span {
  font-weight: 700;
  color: #115dea;
  margin: 0 0 20px 5px;
}
.home_box_content button {
  font-size: 25px;
  font-weight: 600;
  background-color: #115dea;
  color: #fff;
  padding: 6px 20px;
  border-radius: 7px;
  margin-top: 20px;
}
.home_box_content button:hover {
  background-color: #fff;
  color: #115dea;
  border: 2px solid #115dea;
  transition: 0.3s ease;
}

/* ------------------------------------------------------------------------------- */

.home_box_ul {
  position: fixed;
  bottom: 30px;
  left: 10px;
}
.header_ul li {
  background-color: #115dea;
  color: #fff;
  margin-bottom: 5px;
  padding: 6px;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.header_ul li:hover {
  border: 2px solid #115dea;
  background-color: #fff;
  color: #115dea;
  cursor: pointer;
  transition: 0.3s ease;
}

.home_up_arrow {
  position: fixed;
  bottom: 30px;
  right: 10px;
  color: #fff;
  background-color: #115dea;
  border-radius: 50%;
  font-size: 30px;
  font-weight: 700;
}
.home_up_arrow:hover {
  border: 1px solid #115dea;
  color: #115dea;
  background-color: #fff;
  cursor: pointer;
  transition: 0.3s ease;
}

html {
  transition: 0.6s ease;
}

