
.aboutsplit {
   width: 90%;
   margin: auto;
   line-height: 50px;
   margin-left: 150px;
   
}


.about {
    background-color: #ffffff;;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    width: 100%;
}

.about .container {
    width: 1050px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.about h2 {
    font-size: 3rem;
}

.about .col-2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: 2.5rem;
}

.about .col-2 p:first-of-type {
    margin: 2rem 0;
}
/* 
.about .col-2 p:nth-child(4) {
    font-size: 1.5rem;
} */

@media screen and (max-width:940px) {
    .about .container {
        grid-template-columns: 1fr;
    }

    .about .container {
        width: 100%;
    }

    .about .container img {
        padding: 0rem;
        width: 90vw;
    
    }

  

    .about .container .col-2 {
        padding: 0;
    }

    .about button {
        margin-bottom: 1rem;
    }
}
